@import '../utils/variables';
@import '../utils/mixins.scss';

.flashlight__modal {
  & h3 {
    margin-bottom: 15px;
  }

  &-content {
    position: relative;
    width: 90%;
    height: 300px;
    margin: 0 auto;
    overflow: hidden;
    border-radius: 32px;
  }

  & img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.overlay {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  background: radial-gradient(
    circle 5vmax at $highlight-x $highlight-y,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.5) 80%,
    rgba(0, 0, 0, 1) 100%
  )
}


